
//СТИЛІ ДЛЯ КНОПОК СЛАЙДЕРА
.slider-dashes {
  display: inline-block;
  text-align: center;
  margin-top: 32px;
  margin-bottom: 100px;
  line-height: 0;

  li {
    position: relative;
    width: 15px;
    display: inline-block;
    margin: 0 5px;
    height: 3px;
    padding: 0;
    background-color: #dcdcdc;
    transition: all 0.4s;
    &.slick-active {
      width: 30px;
      background-color: red;
    }

    button {
      position: absolute;
      top: 0;
      left: 0;
      display: inline-block;
      height: 100%;
      opacity: 0;
      width: 100%;
      border: none;
    }
  }
}

//СТИЛІ ДЛЯ МОДАЛЬНОГО ВІКНА
.modal {
  &-content {
    position: relative;
    padding: 45px 70px 30px 60px;

    .close {
      position: absolute;
      top: 15px;
      right: 15px;
      z-index: 100;
      height: 40px;
      width: 40px;
      border: 1px solid grey;
      &:hover {
        color: red;
      }
    }
  }
  &-dialog {
    width: 1020px;
    min-width: 1020px;
  }
  &-body {
    color: #575757;
    padding-right: 50px;
    &-photo {
      padding: 0;
      float: left;
    }

    &__speaker-info {
      font-size: 0.875em;
      padding-left: 10px;
    }

    &__speaker-text {
      p {
        margin-bottom: 5px;
      }
      line-height: 1.2;
      margin: 60px 30px 0 0;
    }

    &__list-title {
      margin-top: 25px;
      display: block;
      font-weight: 700;
      line-height: 1.5;
    }

    &__list {
      padding-left: 0;
      margin-top: 10px;
      list-style: none;
    }

    &__list-item {
      &::before {
        content: "\f00c";
        font-family: 'Font Awesome 5 Free', serif;
        font-weight: 700;
        display: inline-block;
        padding-right: 20px;
        color: red;
      }
    }
  }
  &-header {
    padding: 15px 0 15px 0;
    float: right;
  }
  &-title {
    display: inline-block;
    font-size: 1.125em;
    color: #575757;
    font-weight: 700;
    text-transform: uppercase;
    padding: 0;
    position: relative;
    &::after {
      position: absolute;
      bottom: -15px;
      content: "";
      display: block;
      width: 100%;
      height: 3px;
      background-color: $font-hover-color;
    }
  }
}